<template>
 <div>
   <h2>Vue SFC and Vue Router Tutorial by Anthony Sciarini</h2>
   <div id="control">
    <label for="inputrange">Will fetch {{userCount}} random user(s)</label>
    <input type="range" id="inputrange" min="1" max="10" v-model="userCount">
     	<button v-on:click="getUser">Fetch</button>
        <button v-on:click="resetUser">Reset</button>
   </div>

   <table v-show="users.length > 0">
     <tr>
		<th>Name</th>
		<th>Username</th>
                <th>Profile</th>
		<th>Details</th>
     </tr>
     
     <template v-for="u in users">
       <tr>
		<td>{{u.name.first}} {{u.name.last}}</td>
		<td>{{u.login.username}}</td>
                <td><img v-bind:src="u.picture.thumbnail"></td>

		<td> 
		 <button v-on:click="showDetails(u)">
			Get Them Deets!
                 </button>
		</td>
       </tr>
     </template>
  </table>
 </div>
</template>

<script>

 var myGlobal = {}; // "global" storage for this component.

 export default {
   props: {
	//your prop variables here
   },
   data() { // Notice that data is now a FUNCTION
     return {
       userCount: 4,
       users: []
     }
   },
   mounted() {	//HERE BE THE LIFE CYCLE FUNCTIONS
	this.users = myGlobal.users;
   },
   methods: {
     getUser() {
      fetch(`https://randomuser.me/api?results=${this.userCount}`)
      .then(r => r.json())  // convert the response 'r' to a JSON object
      .then(u => {
	    this.users = u.results;
            myGlobal.users = u.results;
   });
     },
     resetUser() {
       this.users = [],
       myGlobal.users = []	
     },
     showDetails(who) {
        this.$router.push({
  		name: 'user-details',
		params: {
			user : who
		}
        })
     }
  },
}
</script>
